<template>
  <base-component>
    <Breadcrumb :style="{margin: '15px 0 15px 185px', position: 'fixed'}">
      <BreadcrumbItem to="/home"></BreadcrumbItem>
      <BreadcrumbItem to="/mqDisplay"></BreadcrumbItem>
    </Breadcrumb>
    <Content class="content" :style="{padding: '10px', background: '#fff', margin: '50px 0 1px 185px', zIndex: '1'}">
      <div style="padding: 1px 1px ">
        <br>
        <Row :gutter=10 align="middle">
          <Col span="5">
            <Card :padding=5>
              <template #title>DB</template>
              <Input prefix="ios-search" v-model="dbNames" placeholder="DB " @on-enter="getReplications()">
              </Input>
            </Card>
          </Col>
          <Col span="8">
            <Card :padding=5>
              <template #title></template>
              <Input prefix="ios-search" v-model="srcTbl" placeholder="" @on-enter="getReplications()">
              </Input>
            </Card>
          </Col>
          <Col span="8">
            <Card :padding=5>
              <template #title>topic</template>
              <Input prefix="ios-search" v-model="dstTopic" placeholder="Topic" @on-enter="getReplications()">
              </Input>
            </Card>
          </Col>

          <Col span="3">
            <Row :gutter=10 align="middle">
              <Button type="primary" icon="ios-search" :loading="dataLoading" @click="getReplications()"></Button>
              <i-switch v-model="otterSearchMode" size="large" style="margin-left: 10px">otter
                <template #open>
                  <span>otter</span>
                </template>
                <template #close>
                  <span>otter</span>
                </template>
              </i-switch>
            </Row>
            <Row :gutter=10 align="middle" style="margin-top: 20px">
              <Button icon="md-refresh" :loading="dataLoading" @click="resetParam"></Button>
            </Row>
          </Col>
        </Row>
        <br>
        <Table :columns="columns" :data="dataWithPage" :loading="dataLoading" border>
          <template slot-scope="{ row, index }" slot="panel">
            <Button type="success"  style="margin-right: 5px" @click="goToHickwallMonitorPageDelay(row, index)">
              
            </Button>
            <Button type="primary"  style="margin-right: 5px" @click="goToHickwallMonitorPageTPS(row, index)">
              TPS
            </Button>
          </template>
        </Table>
        <div style="text-align: center;margin: 16px 0">
          <Page
            :total="total"
            :current.sync="current"
            show-sizer
            show-total
            show-elevator
            @on-change="dataWithPage"
            @on-page-size-change="handleChangeSize"></Page>
        </div>
        <br>
      </div>
    </Content>
  </base-component>
</template>

<script>
import 'codemirror/theme/monokai.css'
import 'codemirror/mode/xml/xml.js'

import 'codemirror/addon/fold/foldgutter.css'
import 'codemirror/addon/fold/foldgutter.js'

export default {
  name: 'MqDisplay',
  data () {
    return {
      totalData: [
        {
          title: 'otter DB',
          key: 'otterDbCount'
        },
        {
          title: 'otter topic',
          key: 'otterTopicCount'
        },
        {
          title: 'messenger DB',
          key: 'messengerDbCount'
        },
        {
          title: 'messenger topic',
          key: 'messengerTopicCount'
        }
      ],
      columns: [
        {
          title: 'DB',
          key: 'dbName',
          align: 'center'
        },
        {
          title: '',
          key: 'srcLogicTableName',
          align: 'center'
        },
        {
          title: 'topic',
          key: 'dstLogicTableName',
          align: 'center'
        },
        {
          title: '',
          key: 'mhaName',
          align: 'center'
        },
        {
          title: '(hickwall)',
          slot: 'panel',
          align: 'center'
        }

      ],
      // page
      total: 0,
      current: 1,
      size: 10,
      // query param
      srcTbl: this.$route.query.srcTbl,
      dstTopic: this.$route.query.dstTopic,
      dbNames: this.$route.query.dbNames,
      otterSearchMode: this.$route.query.otterSearchMode === true || this.$route.query.otterSearchMode === 'true',
      // get from backend
      totalCount: [{
        otterDbCount: null,
        otterTopicCount: null,
        messengerDbCount: null,
        messengerTopicCount: null
      }],
      records: [],

      dataLoading: false,
      totalCountLoading: false,

      delayPanelPrefixFat: 'http://bat.fws.qa.nt.ctripcorp.com/d/qFmdZpCSz/drc-mha-qmq?from=now-24h&to=now&orgId=0&viewPanel=4&kiosk=tv',
      tpsPanelPrefixFat: 'http://bat.fws.qa.nt.ctripcorp.com/d/qFmdZpCSz/drc-mha-qmq?from=now-24h&to=now&orgId=0&viewPanel=2&kiosk=tv',
      delayPanelPrefixProd: 'https://bat.fx.ctripcorp.com/d/vWeGmjFVk/drc-mha?from=now-24h&to=now&orgId=0&viewPanel=10&kiosk=tv',
      tpsPanelPrefixFatProd: 'https://bat.fx.ctripcorp.com/d/vWeGmjFVk/drc-mha?from=now-24h&to=now&orgId=0&viewPanel=20&kiosk=tv'

    }
  },
  computed: {
    dataWithPage () {
      const data = this.records
      const start = this.current * this.size - this.size
      const end = start + this.size
      return [...data].slice(start, end)
    }
  },
  methods: {
    async getReplications () {
      const that = this
      if (that.dataLoading) return false
      that.dataLoading = true
      const params = {
        dbNames: this.dbNames,
        topic: this.dstTopic,
        srcTblName: this.srcTbl
      }
      if (this.otterSearchMode) {
        params.queryOtter = true
      } else {
        params.queryOtter = false
      }
      const reqParam = this.flattenObj(params)
      console.log(reqParam)
      await that.axios.get('/api/drc/v2/messenger/ReplicationSyncStatus', { params: reqParam })
        .then(res => {
          const pageResult = res.data.data
          if (!pageResult || pageResult.totalCount === 0) {
            that.total = 0
            that.current = 1
            that.records = []
            that.$Message.warning('')
          } else {
            that.records = pageResult
            that.total = pageResult.length
            that.current = 1
            that.$Message.success('')
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    flattenObj (ob) {
      const result = {}
      for (const i in ob) {
        if ((typeof ob[i]) === 'object' && !Array.isArray(ob[i])) {
          const temp = this.flattenObj(ob[i])
          for (const j in temp) {
            result[i + '.' + j] = temp[j]
          }
        } else {
          result[i] = ob[i]
        }
      }
      return result
    },
    handleChangeSize (val) {
      this.size = val

      this.$nextTick(() => {
        this.getReplications()
      })
    },
    resetParam () {
      this.srcTbl = null
      this.dstTopic = null
      this.dbNames = null
    },
    goToHickwallMonitorPageDelay (row, index) {
      this.$Message.info('...')
      this.$Modal.remove()
      this.$forceUpdate()
      if (window.location.href.includes('fat')) {
        const url = this.delayPanelPrefixFat + '&var-mha=' + row.mhaName + '&var-auto_gen_other_mha=All'
        window.open(url, '_blank')
      } else {
        const url = this.delayPanelPrefixProd + '&var-mha=' + row.mhaName + '&var-auto_gen_other_mha=All'
        window.open(url, '_blank')
      }
    },
    goToHickwallMonitorPageTPS (row, index) {
      this.$Message.info('...')
      this.$Modal.remove()
      this.$forceUpdate()
      if (window.location.href.includes('fat')) {
        const url = this.tpsPanelPrefixFat + '&var-mha=' + row.mhaName + '&var-auto_gen_other_mha=All'
        window.open(url, '_blank')
      } else {
        const url = this.tpsPanelPrefixFatProd + '&var-mha=' + row.mhaName + '&var-auto_gen_other_mha=All'
        window.open(url, '_blank')
      }
    }
  },
  created () {
    this.axios.get('/api/drc/v2/permission/meta/dbReplication/query').then((response) => {
      if (response.data.status === 403) {
        this.$router.push('/nopermission')
        return
      }
      this.getReplications()
    })
  }
}
</script>

